<template>
    <div>
        <v-snackbar
            v-model="snackbar"
            top
            right
            :color="color"
            >
            {{msg}}
            <v-btn
            color="black"
            text
            @click="snackbar = false"
            >
            
            </v-btn>
  
            </v-snackbar>
    <v-tabs  v-model="tab" background-color="primary" dark>
       <v-tab  v-for="(item,i) in tabslist" :key="i">{{item}}</v-tab>
     </v-tabs>
     <v-tabs-items v-model="tab">
      <v-tab-item tab="0">
        <v-card>
           <!-- data table list  -->
           <v-card-title>
      
      <v-spacer></v-spacer>
      <v-text-field
        v-model="search"
        append-icon="mdi-magnify"
        label="Search"
        single-line
        hide-details
      ></v-text-field>
    </v-card-title>
     <v-data-table
         item-key="id"
        :headers="headers"
        :items="clientList"
        :search="search"
        
      >  
      <template v-slot:[`item.actions`]="{item}" >
          <div >
            <v-btn color="success" class="mx-2" :disabled="isAdmin != 'Administrator' " fab x-small @click="editService(item)" outlined>
              <i class="fa fa-pencil" aria-hidden="true"></i>
            </v-btn>
            <v-btn :disabled="isAdmin != 'Administrator' || item.AttendedBy != loggedUser" color="red" fab x-small @click="deleteService(item._id)" outlined>
              <i class="fa fa-trash" aria-hidden="true"></i>
            </v-btn>
            <v-btn :disabled="isAdmin != 'Administrator'" color="info" class="mx-2" fab x-small @click="details_(item._id)" outlined>
              <i class="fa fa-eye" aria-hidden="true"></i>
            </v-btn>
            
           
          </div>
         
           
          </template>
      <template v-slot:no-results>
              <v-alert :value="true" color="danger"  >
                Your Search {{search}} does not exist.
              </v-alert>
          </template>
      </v-data-table>
           <!-- data table list ends here  -->
        </v-card>
      </v-tab-item>
      <v-tab-item tab="1">
        <v-card>
           <!-- add client section  -->
           <v-form   style="padding:15px;">
            <v-row>
                <v-col 
            cols="12"
            md="3">
            <div >
               <img @click="$refs.imgsol.click()" v-if="previewurl_" :src="previewurl_"  style="width:75px;"/>
             
            </div>
            <label>Photo</label>
            <input type="file" ref="imgsol"  @change="onFileChange_S" hidden/>
          </v-col>
                <v-col cols="12" md="3">
             <v-text-field
              v-model="Client_.ClientName"
              label="Name"
              dense
              hide-details
            ></v-text-field>
                </v-col>
                <v-col cols="12" md="3">
             <v-text-field
              v-model="Client_.Username"
              label="Username"
              dense
              hide-details
            ></v-text-field>
                </v-col>
                <v-col cols="12" md="3">
             <v-text-field
              v-model="Client_.Permissions"
              label="Permissions"
              dense
              hide-details
            ></v-text-field>
                </v-col>
               
               
            <v-col cols="12" md="4">
            <v-btn color="primary" type="button" v-if="!edited" @click="saveServicepriority">
              Submit
            </v-btn>
            <!-- updateservice -->
            <v-btn color="info" type="button" v-if="edited" @click="editupload" >
              Update
            </v-btn>
            <v-btn
              type="button"
              @click="FormReset"
              class="mx-2"
            >
              Reset
            </v-btn>
          </v-col>
            </v-row>
        </v-form>
             <!-- add client section  ends here-->
        </v-card>
      </v-tab-item>
      </v-tabs-items>
    </div>
</template>
<script>
import axios from 'axios'
import { defineComponent ,ref, reactive,onMounted} from '@vue/composition-api'

export default defineComponent({
    setup() {
        onMounted(() => {
           getClientList()
        })
        
        let tab = ref(0)
        let clientList = ref ([])
        let tabslist = reactive(['Clients List', 'Add Clients'])
        let search = ref('')
        const headers = reactive ([
          {text: 'Client Name', value: 'ClientName', sortable: true},
          {text: 'username', value: 'Username', sortable: true},
          {text: 'Permissions', value: 'Permissions', sortable: true},
          {text: 'Status', value: 'Status', sortable: true},
          {text: 'Date', value: 'createdDate', sortable: true},
          {text: 'Action', value: 'actions', sortable: false, width: '180px'},
        ])
        let snackbar = ref(false)
        let color = ref('')
        let msg = ref('')
        let previewurl_ = ref('https://us.123rf.com/450wm/pavelstasevich/pavelstasevich1811/pavelstasevich181101028/112815904-no-image-available-icon-flat-vector-illustration.jpg?ver=6')
        let Client_ = reactive(
            {
              ClientName: ''
            }       
        )
        //get client list method
        async function getClientList()
        {
              let apiURL = 'api/masterAPI/getclients';
              axios.get(apiURL).then(res => {        
                clientList.value = res.data;      
              }).catch(error => {
                console.log(error)
             });
        }
        async function onFileChange_S(e)
        {
            const selectedFile_ = e.target.files[0]; // accessing file
            // selectedFile_ = selectedFile_;
            previewurl_.value = URL.createObjectURL(selectedFile_);
        }
        

        return{
            clientList,
            tab,
            tabslist,
            getClientList,
            search,
            headers,
            snackbar,
            color,
            msg,
            Client_,
            previewurl_,
            onFileChange_S

        }
        
    },
})
</script>
